html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

.usajobs-assistive-text {
  position: absolute !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  clip: rect(0 0 0 0) !important;
}

@media screen and (min-width: 37.5em) {
  .usajobs-joa-pilot__shell {
    padding-top: 2rem;
  }
}

@media screen and (min-width: 62em) {
  .usajobs-joa-pilot__shell {
    padding-top: 3rem;
  }
}

@media screen and (min-width: 62em) {
  .usajobs-joa-pilot__content-well {
    float: left;
    display: block;
    margin-right: 2.35765%;
    width: 57.35098%;
  }
  .usajobs-joa-pilot__content-well:last-child {
    margin-right: 0;
  }
}

.usajobs-joa-pilot__aside {
  padding: 0 1.5rem;
}

@media screen and (min-width: 48em) {
  .usajobs-joa-pilot__aside {
    padding: 0;
  }
}

@media screen and (min-width: 62em) {
  .usajobs-joa-pilot__aside {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #f1f1f1;
    width: 31.76157%;
  }
}

@media screen and (min-width: 37.5em) {
  .usajobs-joa-pilot__accordion-trigger {
    position: absolute !important;
    margin: -1px !important;
    border: 0 !important;
    padding: 0 !important;
    width: 1px !important;
    height: 1px !important;
    overflow: hidden !important;
    clip: rect(0 0 0 0) !important;
  }
}

.usajobs-joa-nav--pilot {
  z-index: 100;
}
